<template>
  <div>
    <el-divider content-position="left">请求信息：{{mainData.summary}} createBy {{mainData.description}}</el-divider>
    <div class="main-header">
      <el-input
        title="点击复制"
        class="btn-debbuger"
        @click.native="copy(mainData.url)"
        placeholder="请输入内容"
        v-model="mainData.url"
      >
        <el-button type="primary" slot="prepend">{{mainData.method}}</el-button>
      </el-input>
      <el-button class="btn-debbuger" type="primary" @click="submit">测试请求数据</el-button>
    </div>

    <el-divider content-position="left">请求参数</el-divider>

    <div class="main-request">
      <!-- <span>请求参数：{{mainData.parameters}}</span> -->
      <el-form :model="query" label-width="100px" class="request-ruleForm">
        <el-form-item label="参数名">参数值</el-form-item>
        <!-- <span>参数类型</span> -->
        <div class="main-line" v-for="d in mainData.parameters" :key="d.name">
          <el-form-item :label="d.name">
            <el-upload
              v-if="d.type=='file'"
              :action="mainData.url"
              :auto-upload="false"
              multiple
              :limit="3"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <el-input
              v-else
              v-model="query[d.name]"
              class="main-line-input"
              :placeholder="d.description"
            ></el-input>
          </el-form-item>
          <el-divider direction="vertical"></el-divider>

          <span>{{ d.type }}</span>
        </div>
      </el-form>

      <div class="request-json">
        <el-button type="primary" @click="onCopy(params)">点击复制</el-button>

        <div>
          <span v-html="params"></span>
          <p>{{params}}</p>
        </div>
        <!-- <json-viewer :value="params" :expand-depth="2"  boxed sort></json-viewer> -->
      </div>
    </div>

    <el-divider content-position="left">返回参数</el-divider>
    <!-- {{ params }} -->
    <div class="main-response">
      <div class="info">
        <div>
          {{mainData.responsesData.title}}
          <el-divider direction="vertical"></el-divider>
          {{mainData.responsesData.description}}
        </div>
        <div>
          名字
          <el-divider direction="vertical"></el-divider>类型
          <el-divider direction="vertical"></el-divider>描述
          <!-- {{mainData.responsesData.properties}} -->
          <div v-for="(item,index) in mainData.responsesData.properties">
            {{index}}
            <el-divider direction="vertical"></el-divider>
            {{item.type}}
            <el-divider direction="vertical"></el-divider>
            {{item.description}}
            <el-divider direction="vertical"></el-divider>
            <!-- {{item}} -->
          </div>
        </div>
      </div>
      <json-viewer
        class="json"
        :value="responseData"
        :expand-depth="3"
        boxed
        sort
        style="max-height: auto;!important"
      ></json-viewer>
    </div>

    <el-divider content-position="left">底线</el-divider>

    <div>
      <!-- <span>{{mainData}}</span> -->
    </div>
  </div>
</template>
<script>
export default {
  name: "mainDiv",
  data() {
    return {
      query: {},
      prefix: "",
      responseData: {}
    };
  },
  props: {
    mainData: {}
  },
  mounted() {},
  
  methods: {
    onCopy(v) {
      var reg = "</br>";
      let d = v.replace(new RegExp("</br>",'gm'), "\n ");
      
      this.copy(d);
    },
    copy(v) {
      this.$copyText(v).then(res => {
        this.$message({
          message: "成功复制：" + v,
          type: "info",
          showClose: true
        });
      });
    },
    init() {
      this.responseData = {};
      this.prefix = this.mainData.operationId.split("Using")[0];
    },
    async submit() {
      let res = await this.$http({
        url: this.mainData.url,
        method: this.mainData.method,
        data: this.query
      });
      this.responseData = res.data;
    }
  },

  computed: {
    params() {
      if (this.mainData.parameters) {
        let result = this.prefix + "Params: {</br>";
        this.mainData.parameters.forEach(e => {
          result =
            result +
            e.name +
            ":'', // " +
            (e.description ? e.description : "") +
            "</br>";
        });
        result = result + "}";
        return result;
      } else {
        return "";
      }
    }
  }
};
</script>
<style scoped>
.btn-debbuger {
  width: 400px;
  margin-left: 50px;
}
.main-header {
}
.main-header .message {
  margin-left: 50px;
  font-size: 30px;
}
.main-request {
  display: flex;
  justify-content: space-around;
}
.main-line {
  display: flex;
  height: 50px;
}
.main-line-input {
  width: 200px;
}
.request-ruleForm {
  width: 45%;
}
.request-json {
  width: 45%;
}
.main-response .json {
  width: 50%;
}
.main-response .info {
  margin-left: 30px;
  width: 35%;
}
.main-response {
  height: 100%;
  display: flex;
  /* justify-content: space-between; */
}
</style>



