<template>
  <div>
    <el-container>
      <el-header>
        <div class="header-title">{{doc?doc.info.title:''}}</div>
        <div class="header-seach">
          <el-input placeholder="搜索" v-model="searchName" @keyup.enter.native="enterSearch">
            <el-button type="primary" @click="enterSearch" icon="el-icon-search" slot="append"></el-button>
          </el-input>
        </div>

        <div class="header-right">
          <el-button type="success" @click="clearAndSerch">刷新菜单</el-button>
          <el-button type="primary" @click="dialogVisible = true">登录</el-button>
        </div>
      </el-header>
      <el-container>
        <el-aside width="250px" class="el-aside-vertical">
          <!-- background-color="#545c64" -->
          <el-menu
            class="el-menu-vertical"
            background-color="rgb(17, 23, 29)"
            text-color="#fff"
            active-text-color="#ffd04b"
          >
            <el-submenu :index="index.toString()" v-for="(item,index) in menuList" :key="index">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>{{ item.name }}</span>
              </template>
              <el-menu-item
                @click="onClickMenu(d)"
                :index="index+d.operationId"
                :key="index+d.operationId"
                v-for="d in item.child"
              >{{d.summary}}</el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>

        <el-main>
          <main-div ref="mainData" v-if="showMain" :mainData="mainData"></main-div>
        </el-main>
      </el-container>
    </el-container>
    <el-dialog title="登录操作" :visible.sync="dialogVisible" width="width">
      <el-form>
        <el-form-item label="账号：" label-width="100px">
          <el-input v-model="username"></el-input>
        </el-form-item>
        <el-form-item label="密码：" label-width="100px">
          <el-input type="password" v-model="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="login">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import mainDiv from "./main";
import { debug, debuglog } from "util";
import axios from "axios";

export default {
  data() {
    return {
      dialogVisible: false,
      username: "",
      password: "",
      searchName: "",
      allMenuList: [],
      menuList: [],
      mainData: {},
      definitions: {},
      showMain: false,
      doc: { info: {} }
    };
  },
  components: {
    mainDiv
  },
  created() {
    this.getDoc();
  },
  methods: {
    clearAndSerch() {
      this.searchName = "";
      this.getDoc();
    },
    enterSearch() {
      this.menuList = [];
      let allMenuList = JSON.parse(localStorage.getItem("menu"));
      allMenuList.filter(e => {
        let r = e.name.includes(this.searchName);
        if (r) {
          this.menuList.push(e);
          return true;
        } else {
          let child = [];

          if (e.child && e.child.length > 0) {
            child = e.child.filter(c => {
              return (
                c.url.includes(this.searchName) ||
                c.summary.includes(this.searchName) ||
                c.tags[0].includes(this.searchName)
              );
            });
            // return e.child.length > 0;
          }
          if (child.length > 0) {
            this.menuList.push({
              name: e.name,
              description: e.description,
              child
            });
          }
        }
      });
    },
    async login() {
      let res = await this.$http({
        url: "/api/login",
        method: "post",
        data: {
          username: this.username,
          password: this.password
        }
      });
      if (res.data.ret === 0) {
        localStorage.setItem("token", res.data.data);
        this.dialogVisible = false;
        this.$message({
          message: "登录成功",
          type: "success",
          showClose: true
        });
      } else {
        this.$message({
          message: res.data.msg,
          type: "info",
          showClose: true
        });
      }
    },
    onClickMenu(d) {
      this.mainData = d;
      this.showMain = true;
      let refType = this.mainData.responses[200].schema.$ref.split("/")[2];
      this.mainData.responsesData = this.doc.definitions[refType];
      this.$nextTick(() => {
        this.$refs.mainData.init();
      });
    },
    async getDoc() {
      const r = await this.$http({
        url: "/v2/api-docs",
        method: "get"
      });
      this.doc = r.data;
      localStorage.setItem("definitions", JSON.stringify(r.data.definitions));
      const paths = this.doc.paths;
      let menuData = [];
      menuData = this.doc.tags;
      for (const url in paths) {
        let result = {};
        result.url = url;
        if (paths.hasOwnProperty(url)) {
          const element = paths[url];
          for (const method in element) {
            result.method = method;
            if (element.hasOwnProperty(method)) {
              const e = element[method];
              for (const key in e) {
                if (e.hasOwnProperty(key)) {
                  result[key] = e[key];
                }
              }
            }
          }
        }
        for (const e of menuData) {
          if (e.name == result.tags[0]) {
            if (!e.child) {
              e.child = [];
            }
            e.child.push(result);
          }
        }
      }
      this.allMenuList = this.menuList = menuData;
      localStorage.setItem("menu", JSON.stringify(this.allMenuList));
    }
  }
};
</script>

<style scoped>
.el-header {
  background-color: #23262e;
  color: white;
  line-height: 60px;
  display: flex;
}
.header-seach {
  margin-left: 30px;
}
.header-right {
  margin-left: 30px;
  float: right;
}
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  /* overflow: hidden; */
  background-color: rgb(17, 23, 29);
  color: #333;
  text-align: left;
  height: calc(100vh - 60px);
}

.el-menu {
  /* height: 100vh; */
}
.el-main {
  height: calc(100vh - 60px);
  background-color: #e9eef3;
  color: #333;
  text-align: left;
  line-height: 30px;
}

body > .el-container {
  margin-bottom: 40px;
}
</style>



